@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:400,700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

a {
  color: white;
  text-decoration: none;
}

html {
  font-size: 16px;
  font-weight: normal;
  font-family: 'Noto Sans KR', sans-serif;
  color: #333333;
}

#topPopUp {
  min-width: 1050px;
  height: 42px;
  text-align: center;
  line-height: 42px;
  background-color: #748ffc;
  color: white;
  font-size: 18px;
  -webkit-transition: display 1s, opacity 1s;
  transition: display 1s, opacity 1s;
}

#topPopWrap {
  width: 1050px;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

#topPopCls {
  position: absolute;
  top: 6px;
  right: 10px;
  opacity: 0.5;
  width: 30px;
  height: 30px;
  cursor: pointer;
}

#topPopCls:hover {
  opacity: 1;
}

header {
  height: 156px;
}

#headerTop {
  width: 1060px;
  height: 37px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#headerDeli {
  width: 163px;
  height: 22px;
  background-image: url(data:image/gif;base64,);
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;
  margin-left: 30px;
}

#userMenu {
  margin-right: -20px;
}

.userItem {
  float: left;
  margin-right: 20px;
  font-size: 12px;
  cursor: pointer;
}

.strong {
  color: #748ffc;
  font-weight: bold;
}
#headerCen {
  width: 1050px;
  height: 63px;
  margin: 0 auto;
  position: relative;
}
#logoImg {
  width: 99px;
  height: 66px;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: -8px;
}

#headerBott {
  width: 100%;
  height: 55px;
  box-shadow: 0 5px 5px 0px rgba(0, 0, 0, 0.03);
  background-color: white;
}
#headerBottWrap {
  width: 1050px;
  margin: 0 auto;
}

#mainMenu {
  float: left;
}

#mainMenu li {
  float: left;
  padding: 0 50px;
  height: 55px;
  line-height: 55px;
  cursor: pointer;
}

#mainMenu li:first-child {
  padding-left: 20px;
}

#mainMenu li img {
  display: block;
  margin-right: 10px;
  float: left;
  margin-top: 21px;
}

#searchWrap {
  width: 230px;
  height: 36px;
  position: relative;
  float: left;
  margin-top: 9.5px;
}

#search {
  width: 100%;
  height: 100%;
  border-radius: 18px;
  border: none;
  background-color: #f7f7f7;
  font-size: 12px;
  padding: 0px 36px 0 18px;
}

#searchWrap img {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 0;
  right: 15px;
}

#shoppingCartImg {
  display: inline-block;
  margin-top: 12px;
  margin-left: 7.5px;
}

